<template>
  <div id="codeHeader">
    <el-button round type="success" @click="execute">点击运行</el-button>
    <div style="flex-grow: 1"></div>
    <span id="codeHeaderText">源代码</span>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CodeHeader",
  props: ['codeInput'],
  setup(props) {
    function execute() {
      axios({
        method: 'POST',
        url: 'http://localhost:9090',
        data:{codeInput:props.codeInput}
      }).then(
          res => {
            console.log(res)
          },
          err=>{
            console.log(err)
          }
      )
      // uploadCode(props.codeInput,'code.txt')
    }

    return {
      execute
    }
  }
}
</script>

<style scoped>
#codeHeader {
  display: flex;
  flex-direction: row-reverse;
  padding: 5px;
}

#codeHeaderText {
  margin-left: 7px;
  color: #777777;
  line-height: 30px;
}
</style>