<template>
  <div class="displayUnitContainer" @click="showDialog">
    <el-scrollbar>
      <slot name="basicContent"></slot>
    </el-scrollbar>
    <el-scrollbar>
      <el-dialog v-model="isShow">
        <template #header>
          <slot name="dialogTitle"></slot>
        </template>
        <div class="dialogContent">
          <el-scrollbar>
            <slot name="dialogContent"></slot>
          </el-scrollbar>
        </div>
      </el-dialog>
    </el-scrollbar>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "DisplayUnit",
  setup() {
    let isShow = ref(false);

    function showDialog() {
      isShow.value = true
    }

    return {
      showDialog,
      isShow
    }
  }
}
</script>

<style scoped>
.displayUnitContainer {
  height: 20%;
  border-bottom: 1px solid #d2d2d2;
}
.dialogTitle{

}
.dialogContent{
  height: 60vh;
  border: 1px solid #d2d2d2;
  border-radius: 5px;
}
</style>